import http from '@/components/utils/http.js';
import weCropper from '../../static/weCropper';
const device = uni.getSystemInfoSync();
const width = device.windowWidth;
const height = device.windowHeight - 50;
console.log(device);
export default {
	data() {
		return {
			src:"/static/missing-face.png",
			cropperOpt: {
				id: 'cropper',
				width: width,
				height: height,
				scale: 2.5,
				zoom: 8,
				cut: {
					x: (width - 300) / 2,
					y: (height - 300) / 2,
					width: 300,
					height: 300
				}
			},
			weCropper: ''
		};
	},
	methods: {
		back() {
			uni.redirectTo({
				url: '../infoDetail/infoDetail'
			});
		},
		touchStart(e) {
			this.weCropper.touchStart(e);
		},
		touchMove(e) {
			this.weCropper.touchMove(e);
		},
		touchEnd(e) {
			this.weCropper.touchEnd(e);
		},
		convertBase64UrlToBlob(dataURI, type) {
			var binary = atob(dataURI.split(',')[1]);
			var array = [];
			for (var i = 0; i < binary.length; i++) {
				array.push(binary.charCodeAt(i));
			}
			return new Blob([new Uint8Array(array)], { type: type }, { filename: '1111.jpg' });
		},
		blobToDataURL(blob) {
			var a = new FileReader();
			a.readAsDataURL(blob); //读取文件保存在result中
			a.onload = function(e) {
				var getRes = e.target.result; //读取的结果在result中
				console.log(getRes);
			};
		},
		getCropperImage() {
			let _this = this;
			uni.showLoading({
				title:"头像更新中..."
			})
			let pathurl = _this.apiServer + '/user/upload';//上传服务器地址
			this.weCropper.getCropperImage(avatar => {
				if (avatar) {
					//  获取到裁剪后的图片
					//  获取到裁剪后的图片
					
					
					
					let opts = {
						url: '/base/uploadheadimg/',
						method: 'post'
					};
					let param = {
					};
					
					http.httpTokenUpload(opts,avatar,param).then(
						res => {
							//打印请求返回的数据
							let data=JSON.parse(res.data);
							console.log(data);
							if (data['code'] == 0) {
								
								wx.navigateTo({
								  url: './home'
								})
								
							} else {
								 uni.hideLoading();
								 // uni.showModal({
								 // 	content: data.msg,
								 // 	showCancel: false
								 // });
								 this.$api.msg(data.msg);
								
							}
						},
						error => {
							console.log('uploadImage fail', err);
							uni.showModal({
								content: err.errMsg,
								showCancel: false
							});
							uni.hideLoading();
						}
					);
				} else {
					console.log('获取图片失败，请稍后重试');
				}
			});
		},
		uploadTap() {
			const self = this;

			uni.chooseImage({
				count: 1, // 默认9
				sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success(res) {
					let src = res.tempFilePaths[0];
					//  获取裁剪图片资源后，给data添加src属性及其值
					self.weCropper.pushOrign(src);
				}
			});
		}
	},
	onLoad(options) {
		// do something
		const cropperOpt = this.cropperOpt;
		
		const src = options.src;
		
		//console.log("src",options.src);
		if (src) {
			Object.assign(cropperOpt, {
				src
			});
			

			this.weCropper = new weCropper(cropperOpt)
				.on('ready', function(ctx) {})
				.on('beforeImageLoad', ctx => {
					uni.showToast({
						title: '上传中',
						icon: 'loading',
						duration: 3000
					});
				})
				.on('imageLoad', ctx => {
					uni.hideToast();
				});
		}
	}
};
